import { Injectable } from '@angular/core';
import { Http,Response,Headers } from '@angular/http';
import  'rxjs/Rx';
import {Recipe} from '../recipies/recipe.model';
import {SelectedrecipeService} from './Selectedrecipe.Service';
import { AuthService } from './auth.service';
import {Cart} from '../orders-list/cart.model'
import {CartRecipeService} from './Cartrecipe.service';

@ Injectable()
export class ShopingHttpService {
    constructor (private http : Http, 
        private selservice:SelectedrecipeService,
        private authservice : AuthService,
        private cartservice:CartRecipeService) {}

    storerecipies () {
       return this.http.put('https://shoping-center-4dda1.firebaseio.com/recipies.json?auth='
                            +this.authservice.getToken()
       ,this.selservice.getrecipes());
    }

    getrecipies () {
    //    this.http.get('https://shoping-center-4dda1.firebaseio.com/recipies.json')
    //            .subscribe((response:Response)=>{
    //                const recipies:Recipe[]=response.json();
    //                this.selservice.setrecipes(recipies)}
    //            );
        const headers=new Headers({'Accept':'application/json','CrossDomain':'true','Access-Control-Allow-Origin':'http://localhost:8080'});
        this.http.get('http://localhost:8080/mavenweb1-1.0-SNAPSHOT/rest/recipies/all', {headers: headers})
                .subscribe((response:Response)=>{
                    const recipies1:String=response.text();
                    console.log(recipies1);
                    }
                );
                
     }

  public storeCart(cartrecipieam: {numb:number,rec:Recipe}[],
                    totalprice:number, name:string, email:string,sex:string ){
    const cart=new Cart(name,email,sex,cartrecipieam,totalprice);
    return this.http.post('https://shoping-center-4dda1.firebaseio.com/carts.json',
    cart);  
    }  

  public getCarts () {
    this.http.get('https://shoping-center-4dda1.firebaseio.com/carts.json?auth='
    +this.authservice.getToken())
            .subscribe((response:Response)=>{
                const cartsmix:any[]=response.json();
                let carts:Cart[]=[];
                for (let index in cartsmix) {
                    carts.push((cartsmix[index])); }
                    this.cartservice.setCarts(carts.slice());
                }
            );
 }

 public storeCarts(){
    return this.http.put('https://shoping-center-4dda1.firebaseio.com/carts.json?auth='
                 +this.authservice.getToken(), this.cartservice.getCarts());  
    }
}


